<template>
	<div>
		<h3>Booking Calendar</h3>
		<b-alert variant="success" show>
			Select slot to make a booking
		</b-alert>
		<b-row>
			<b-col md="4">
				<b-form-select
					v-model="country"
					@change="fetchWareHouses"
				>
					<b-form-select-option
						v-for="(country, i) in countries"
						:key="i"
						:value="country._id"
					>
						{{ country.name }}
					</b-form-select-option>
				</b-form-select>
			</b-col>
			<b-col md="4">
				<b-form-select
					v-model="searchForm.warehouse"
					@change="fetch"
				>
					<b-form-select-option
						v-for="(warehouse, i) in warehouses"
						:key="i"
						:value="warehouse._id"
					>
						{{ warehouse.name }}
					</b-form-select-option>
				</b-form-select>
			</b-col>
		</b-row>
		<b-row>
			<b-col>
				<div class="vuecal__flex mt-2">
					<vue-cal
						class="vuecal--blue-theme"
						:active-view="isMobile() ? 'day' : 'week'"
						hide-weekends
						:events="i == 0 ? pallets : containers"
						:selected-date="selectedDate"
						hide-view-selector
						:time-from="6 * 60"
						:time-to="18 * 60"
						@view-change="onViewChange"
						:on-event-click="onEventClick"
						v-for="i in [0, 1]"
						:key="i"
					>
						<template v-slot:no-event>
							<div>No Slot</div>
						</template>
						<template v-slot:event="{ event }">
							<div :style="{cursor: isMoving ? 'move' : 'pointer'}">
								<div>{{ event.isClosed ? 'Closed' : (event.freeCapacity == 0 ? 'Full' : 'Free space:') }}</div>
								<h3>{{ event.isClosed ? '' : (event.freeCapacity == 0 ? '' : event.freeCapacity) }}</h3>
								<div>{{ event.isClosed ? '' : (event.freeCapacity == 0 ? '' : event.class + 's') }}</div>
							</div>
						</template>
					</vue-cal>
				</div>
			</b-col>
		</b-row>
		<h3 class="mt-4">Bookings</h3>
		<b-row>
			<b-col md="4">
				<b-form-select
					v-model="searchForm.company"
					@change="fetch"
				>
					<b-form-select-option value="">All</b-form-select-option>
					<b-form-select-option
						v-for="(company, i) in (user.userType == 'user' ? user.company : companies)"
						:key="i"
						:value="company._id"
					>
						{{ company.name }}
					</b-form-select-option>
				</b-form-select>
			</b-col>
		</b-row>
		<b-table
			:fields='isMobile() ? mobileFields : fields'
			:items='list'
			:busy="loading"
			outlined
			class="mt-3"
			responsive>
			<template #cell(index)="data">
				<p id="tooltip-target-1">{{ (searchForm.page - 1) * searchForm.perPage + data.index + 1 }}</p>
				<b-tooltip target="tooltip-target-1" triggers="hover">
					{{ data.item.index }}
				</b-tooltip>
			</template>
			<template #cell(date)="data">
				<p>{{ moment(data.value).format('DD.MM.YYYY') }}</p>
			</template>
			<template #cell(time)="data">
				<p v-if="data.item.slot">
					{{ data.item.slot.fromTime.hour | hourMinute }} : {{ data.item.slot.fromTime.minute | hourMinute }} ~
					{{ data.item.slot.toTime.hour | hourMinute }} : {{ data.item.slot.toTime.minute | hourMinute }}
				</p>
			</template>
			<template #cell(user)="data">
				<p>{{ data.item.user && data.item.user.username }}</p>
			</template>
			<template #cell(company)="data">
				<p>{{ data.item.company && data.item.company.name }}</p>
			</template>
			<template #cell(slotQty)="data">
				<p>{{ data.value + (data.item.slot && ' ' + data.item.slot.loadUnit + 's') }}</p>
			</template>
			<template #cell(edit)="data">
				<b-button variant="success" size="sm" @click="detail(data.index)" class="mr-2">
					<b-icon icon="file-earmark"></b-icon> {{ isMobile() ? '' : 'Detail' }}
				</b-button>
				<b-button variant="danger" size="sm" @click="deleteById(data.index)" class="mr-2">
					<b-icon icon="trash"></b-icon> {{ isMobile() ? '' : 'Delete' }}
				</b-button>
				<b-button variant="success" size="sm" @click="move(data.index)">
					<b-icon icon="arrows-move"></b-icon> {{ isMobile() ? '' : 'Move' }}
				</b-button>
			</template>
		</b-table>
		<b-modal 
			v-model="bookingModalShow"
			title="Make a booking"
			hide-header-close
			@ok="addBooking"
			@cancel="bookingModalShow=false">
			<b-row>
				<b-col md="4">Date</b-col>
				<b-col md="8">
					{{ moment(bookingCal.date).format('DD.MM.YYYY') }}
				</b-col>
			</b-row>
			<b-row class="mt-4">
				<b-col md="4">Slot Qty</b-col>
				<b-col md="8">
					<b-form-input
						v-model="bookingCal.slotQty"
						type="number"
						:state="!$v.bookingCal.slotQty.numeric ? false : null"
						size="sm"/>
					<b-form-invalid-feedback v-if="!$v.bookingCal.slotQty.numeric">
						Slot Qty should be numeric.
					</b-form-invalid-feedback>
				</b-col>
			</b-row>
			<b-row class="mt-4">
				<b-col md="4">Company</b-col>
				<b-col md="8">
					<b-form-select
						v-model="bookingCal.company"
						size="sm"
						:state="!$v.bookingCal.company.required ? false : null"
					>
						<b-form-select-option
							v-for="(company, i) in (user.userType == 'user' ? user.company : companies)"
							:key="i"
							:value="company._id"
						>
							{{ company.name }}
						</b-form-select-option>
					</b-form-select>
					<b-form-invalid-feedback v-if="!$v.bookingCal.company.required">
						Company is required.
					</b-form-invalid-feedback>
				</b-col>
			</b-row>
			<b-row class="mt-4">
				<b-col md="4">Contact Name</b-col>
				<b-col md="8">
					<b-form-input
						v-model="bookingCal.contactName"
						type="text"
						size="sm"/>
				</b-col>
			</b-row>
			<b-row class="mt-4">
				<b-col md="4">Contact Email</b-col>
				<b-col md="8">
					<b-form-input
						v-model="bookingCal.contactEmail"
						type="text"
						size="sm"
						:state="!$v.bookingCal.contactEmail.email ? false : null"/>
					<b-form-invalid-feedback v-if="!$v.bookingCal.contactEmail.email">
						Email is invalid.
					</b-form-invalid-feedback>
				</b-col>
			</b-row>
			<b-row class="mt-4">
				<b-col md="4">Contact Phone</b-col>
				<b-col md="8">
					<b-form-input
						v-model="bookingCal.contactPhone"
						type="text"
						size="sm"/>
				</b-col>
			</b-row>
			<hr/>
			<div v-for="(v, i) in $v.bookingCal.lines.$each.$iter" :key="i">
				<b-row class="mt-4">
					<b-col md="4">Order Reference</b-col>
					<b-col md="8">
						<b-form-textarea
							v-model="v.orderReference.$model"
							size="sm"
							rows="3"
							:state="!v.orderReference.required ? false : null"/>
						<b-form-invalid-feedback v-if="!v.orderReference.required">
							Order Reference is required.
						</b-form-invalid-feedback>
					</b-col>
				</b-row>
				<b-row class="mt-4">
					<b-col md="4">Attachment</b-col>
					<b-col md="8">
						<b-form-file
							v-model="v.$model.attachment"
						></b-form-file>
					</b-col>
				</b-row>
				<b-row class="mt-4" v-if="i > 0">
					<b-col class="text-right">
						<b-button @click="removeLine(i)" size="sm">
							<b-icon icon="x"></b-icon> Remove Line
						</b-button>
					</b-col>
				</b-row>
				<hr/>
			</div>
			<b-row class="mt-4">
				<b-col class="text-center">
					<b-button @click="addLine" size="sm">
						<b-icon icon="plus"></b-icon> Add Line
					</b-button>
				</b-col>
			</b-row>
		</b-modal>
		<b-modal 
			v-model="detailModalShow"
			title="Booking Detail"
			hide-header-close>
			<b-row>
				<b-col md="4">Date</b-col>
				<b-col md="8">
					<p v-if="booking.date">{{ moment(booking.date).format('DD.MM.YYYY') }}</p>
				</b-col>
			</b-row>
			<b-row class="mt-2">
				<b-col md="4">Time</b-col>
				<b-col md="8">
					<p v-if="booking.slot">
						{{ booking.slot.fromTime.hour | hourMinute }} : {{ booking.slot.fromTime.minute | hourMinute }} ~
						{{ booking.slot.toTime.hour | hourMinute }} : {{ booking.slot.toTime.minute | hourMinute }}
					</p>
				</b-col>
			</b-row>
			<b-row class="mt-2">
				<b-col md="4">Username</b-col>
				<b-col md="8">
					<p v-if="booking.user">{{ booking.user.username }}</p>
				</b-col>
			</b-row>
			<b-row class="mt-2">
				<b-col md="4">Company</b-col>
				<b-col md="8">
					<p v-if="booking.company">{{ booking.company.name }}</p>
				</b-col>
			</b-row>
			<b-row class="mt-2">
				<b-col md="4">Slot Qty</b-col>
				<b-col md="8">
					<p v-if="booking.slot">{{ booking.slotQty + ' ' + booking.slot.loadUnit + 's' }}</p>
				</b-col>
			</b-row>
			<b-row class="mt-2">
				<b-col md="4">Contact Name</b-col>
				<b-col md="8">
					<p>{{ booking.contactName }}</p>
				</b-col>
			</b-row>
			<b-row class="mt-2">
				<b-col md="4">Contact Email</b-col>
				<b-col md="8">
					<p>{{ booking.contactEmail }}</p>
				</b-col>
			</b-row>
			<b-row class="mt-2">
				<b-col md="4">Contact Phone</b-col>
				<b-col md="8">
					<p>{{ booking.contactPhone }}</p>
				</b-col>
			</b-row>
			<div v-for="(line, i) in booking.lines" :key="i">
				<hr>
				<b-row class="mt-2">
					<b-col md="4">Order Reference</b-col>
					<b-col md="8">
						<p>{{ line.orderReference }}</p>
					</b-col>
				</b-row>
				<b-row class="mt-2">
					<b-col md="4">Attachment</b-col>
					<b-col md="8">
						<b-button 
							variant="success" 
							@click.prevent="download(line.attachment)"
							v-if="line.attachment"
							size="sm">
							<b-icon icon="download"></b-icon> <span style="overflow-wrap: anywhere;">{{ line.attachment }}</span>
						</b-button>
					</b-col>
				</b-row>
			</div>
		</b-modal>
		<b-modal 
			v-model="deleteModalShow"
			title="Delete Booking"
			hide-header-close 
			@ok="doDelete"
		>
			Are you sure you want to delete?
		</b-modal>
	</div>
</template>
<script>
	import VueCal from 'vue-cal'
	import { mapState } from 'vuex'
	import moment from 'moment'
	import { required, email, numeric } from 'vuelidate/lib/validators'
	export default {
		name: 'CalendarView',
		components: { VueCal },
		created() {
			window.addEventListener("resize", this.onResize);
		},
		mounted() {
			this.$store.dispatch('calendar/fetchCountries').then(() => {
				if (this.countries.length) {
					this.country = this.countries[0]._id
					this.fetchWareHouses()
				}
			})
		},
		data() {
			return {
				size: window.innerWidth,
				loadUnits: [
					{ id: 1, class: 'pallet', label: 'Pallet' },
					{ id: 2, class: 'container', label: 'Container' }
				],
				selectedDate: new Date(),
				country: '',
				searchForm: {
					warehouse: '',
					company: '',
					startDate: moment().weekday(0).toDate(),
					endDate: moment().weekday(6).toDate(),
					page: 1,
					perPage: 10
				},
				fields: [
					{ key: 'index', label: 'No' },
					{ key: 'date', label: 'Date' },
					{ key: 'time', label: 'Time' },
					{ key: 'user', label: 'User' },
					{ key: 'company', label: 'Company' },
					{ key: 'slotQty', label: 'Qty' },
					{ key: 'edit', label: '-' }
				],
				mobileFields: [
					{ key: 'index', label: 'No' },
					{ key: 'date', label: 'Date' },
					{ key: 'time', label: 'Time' },
					{ key: 'slotQty', label: 'Qty' },
					{ key: 'edit', label: '-'}
				],
				bookingModalShow: false,
				detailModalShow: false,
				deleteModalShow: false,
				bookingCal: {
					slot: '',
					date: '',
					slotQty: 0,
					company: '',
					contactName: '',
					contactEmail: '',
					contactPhone: '',
					lines: []
				},
				booking: {},
				loading: false,
				isMoving: false,
			}
		},
		computed: {
			...mapState({
				countries: state => state.calendar.countries,
				warehouses: state => state.calendar.warehouses,
				containers: state => state.calendar.containers,
				companies: state => state.calendar.companies,
				pallets: state => state.calendar.pallets,
				list: state => state.calendar.list,
				total: state => state.calendar.total,
				user: state => state.auth.user
			})
		},
		validations: {
			bookingCal: {
				slotQty: { numeric },
				company: { required },
				contactEmail: { email },
				lines: {
					$each: {
						orderReference: { required }
					}
				}
			}
		},
		methods: {
			moment,
			onResize(evt) {
				this.size = evt.currentTarget.innerWidth
			},
			isMobile() {
				return this.size <= 1000
			},
			onViewChange({ startDate, endDate, view }) {
				this.selectedDate = view == 'day' ? new Date() : startDate
				this.searchForm.startDate = startDate
				this.searchForm.endDate = endDate
				this.fetch()
			},
			onEventClick(event) {
				if (event.isClosed) {
					this.$toasted.error('Booking Closed.')
					return
				}
				
				if (event.freeCapacity == 0) {
					this.$toasted.error('No Free space.')
					return
				}

				if (this.isMoving) {
					this.doMove(event._id, event.date)
				}
				else {
					this.bookingCal.slot = event._id
					this.bookingCal.date = event.date
					this.bookingCal.slotQty = 0
					this.bookingCal.contactName = ''
					this.bookingCal.contactEmail = ''
					this.bookingCal.contactPhone = ''
					this.bookingCal.lines = [{
						orderReference: '',
						attachment: null
					}]

					this.bookingModalShow = true
				}
			},
			addBooking(evt) {
				evt.preventDefault()

				this.$v.bookingCal.$touch()
				if (this.$v.bookingCal.$error) return

				let formData = new FormData()
				
				formData.append('slot', this.bookingCal.slot)
				formData.append('date', this.bookingCal.date)
				formData.append('slotQty', this.bookingCal.slotQty)
				formData.append('company', this.bookingCal.company)
				formData.append('contactName', this.bookingCal.contactName)
				formData.append('contactEmail', this.bookingCal.contactEmail)
				formData.append('contactPhone', this.bookingCal.contactPhone)
				formData.append('lineCnt', this.bookingCal.lines.length)

				this.bookingCal.lines.forEach((line, i) => {
					formData.append('orderReference_' + i, this.bookingCal.lines[i].orderReference)
					formData.append('attachment_' + i, this.bookingCal.lines[i].attachment)
				})

				this.bookingModalShow = false

				this.$store.dispatch('calendar/booking', formData).then(() => {
					this.$toasted.success('Add Success')
					this.fetch()
				})
			},
			addLine() {
				this.bookingCal.lines.push({ orderReference: "", attachment: null })
			},
			removeLine(idx) {
				this.bookingCal.lines.splice(idx, 1)
			},

			fetchWareHouses() {
				this.$store.dispatch('calendar/fetchWarehouses', this.country).then(() => {
					if (this.warehouses.length) {
						this.searchForm.warehouse = this.warehouses[0]._id
						this.fetch()
					}
					else {
						this.$store.commit('calendar/setContainers', [])
						this.$store.commit('calendar/setPallets', [])
					}
				})
			},
			fetch() {
				this.loading = true
				this.$store.dispatch('calendar/fetch', this.searchForm).then(() => {
					this.loading = false
				})
			},
			detail(idx) {
				this.booking = this.list[idx]
				this.detailModalShow = true
			},
			download(attachment) {
				this.$store.dispatch('calendar/download', attachment).then(res => {
					const blob = new Blob([res.data], { type: 'application/pdf' })
					const link = document.createElement('a')
					link.href = URL.createObjectURL(blob)
					link.download = attachment
					link.click()
					URL.revokeObjectURL(link.href)
				})
			},
			deleteById(idx) {
				this.booking._id = this.list[idx]._id
				this.deleteModalShow = true
			},
			doDelete() {
				this.$store.dispatch('calendar/deleteById', this.booking._id).then(() => {
					this.$toasted.success('Delete Success')
					this.fetch()
				})
			},
			move(idx) {
				this.booking._id = this.list[idx]._id
				this.$toasted.success('Please select a slot on booking calendar to move.')
				this.isMoving = true
			},
			doMove(slot, date) {
				this.isMoving = false
				this.$store.dispatch('calendar/move', { id: this.booking._id, slot, date }).then(() => {
					this.$toasted.success('Move Success')
					this.fetch()
				})
			}
		}
	}
</script>